:root {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    --turquoise-color: invert(82%) sepia(58%) saturate(3603%) hue-rotate(141deg) brightness(88%) contrast(85%);
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

#container {
  min-height: 100vh;
  display:flex;
  flex-direction: column;
}

#header-container {
  flex: 1;
  display:grid;
  align-items: center;
  grid-template-columns: 20% auto 20%;
  border-bottom: 2px solid #e8e8e8;
}

#header {
  grid-column: 2 / 3;
  background-color: white;
  color: #5c5c5c;
  display: flex;
  align-items: center;
}

svg {
  height: 50px;
  filter: var(--turquoise-color);
}

#main-content {
  flex: 10;
  background-color: #f9f9f9;
  display: grid;
  grid-template-columns: 20% auto 20%
}

#left-panel {
  grid-column: 2 / 3;
}

#form-container {
  height: fit-content;
  width: 100%;
  background-color: white;
  border: 2px solid #e8e8e8;
  overflow:hidden;
  margin-bottom: 80px;
  padding: 30px;
}

#forms {
  padding: 0px 30px;
}

#general-input, #education-input, #work-input {
  display:flex;
  flex-direction: column;
}

.input-field {
  padding: 5px 30px;
  display: flex;
  justify-content: stretch;
  align-items: center;
}

label {
  flex: 1;
  color:#797979;
}

input, textarea {
  flex: 4;
  background-color: #f8f8f8;
  border: 2px solid #e8e8e8;
  height: 20px;
  border-radius: 5px;
}

input:active, textarea:active {
  background-color: white;
}

#education-input, #work-input {
  padding-bottom: 10px;
}

.increment-decrement-buttons {
  height: 25px;
  width: 50px;
  background-color: #f9f9f9;
  border: 2px solid #e8e8e8;
  border-radius: 5px;
}

.buttons {
  text-align:center;
}

#submit-align {
  text-align: center;
}

.submit-edit-buttons {
  margin: 40px 0px;
  height: 50px;
  width: 20%;
  background-color: #00008b;
  border-radius: 5px;
  color: white;
  font-weight: bold;
}

#right-panel {
  grid-column: 4 / 5;
}

h3 {
  color: #666666;
}

.h3-grey {
  color: #666666;
}

.h3-black {
  color: black;
  padding-bottom: 5px;
  border-bottom: 1px solid black;
}

h2 {
  color: #5f5f5f;
}

.hide {
  display:none;
}

#name-preview {
  text-align: center;
  font-size: 3em;
  padding-top: 30px;
  padding-bottom: 10px;
}

#other-info {
  text-align: center;
}

.education-first-line, .work-first-line {
  display:grid;
  grid-template-columns: 75% auto;
  padding-bottom: 2px;
}

.school-name-preview, .company-name-preview {
  font-weight: bold;
    
}

.position-preview {
  font-style: italic;
}

.grad-date-preview, .date-preview {
  text-align: right;
}

.university-entry, .work-entry {
  padding-bottom: 20px;
}
